<template>
    <div class="home">
      <div>
        <h1>The Social Travel App</h1>
        <p>[TESTING ONLY] Select Active User</p>
        <select id="users" v-model="activeUser">
            <label for="user">Choose a user:</label>
            <option v-for="user in Users" v-bind:key="user.key" v-bind:value="user.key">
                  {{ user.firstName }}
            </option>
        </select>
      </div>
      <br><br>
      <div class="row">
          <div class="col-md-12">
              <h1>My Trips</h1>
              <div v-if="hasTripsToDisplay">
                  <table class="table table-striped">
                      <thead>
                          <tr>
                              <th
                                    scope="col"
                                    v-for="column in tripTableColumnNames"
                                    v-bind:key="column.sortId"
                                >
                                    {{ column.name}}
                                </th>
                          </tr>
                      </thead>
                      <tbody>
                          <tr v-for="trip in Trips" :key="trip.key">
                              <td>{{ trip.name }}</td>
                              <td>{{ trip.location }}</td>
                              <td>{{ trip.startDate }}</td>
                              <td>{{ trip.endDate }}</td>
                              <td>
                                  <button @click.prevent="deleteTrip(trip.key)" class="btn btn-outline-dark">Edit</button>
                                  <button @click.prevent="deleteTrip(trip.key)" class="btn btn-outline-dark">Delete</button>
                              </td>
                          </tr>
                      </tbody>
                  </table>
              </div>
              <div v-else>
                  <h5>User has no trips to display</h5>
              </div>
          </div>
      </div>
      <br><br>
      <div>
          <h1>My Friends Trips</h1>
          <p>Select a friend to view their trips</p>
          <select id="friends" @change="selectFriend()" v-model="selectedFriend">
              <label for="friend"></label>
              <option v-for="friend in friends" v-bind:key="friend.key" v-bind:value="friend.key">
                    {{ friend.firstName }}
              </option>
          </select>
        </div>
        <div v-if="hasFriendsTripsToDisplay" class="row">
            <div class="col-md-12">
                <table class="table table-striped">
                    <thead>
                        <tr>
                            <th>Name</th>
                            <th>Location</th>
                            <th>Start Date</th>
                            <th>End Date</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="trip in friendsTrips" :key="trip.key">
                            <td>{{ trip.name }}</td>
                            <td>{{ trip.location }}</td>
                            <td>{{ trip.startDate }}</td>
                            <td>{{ trip.endDate }}</td>
                            <td>
                                <button @click.prevent="viewTrip(trip.key)" class="btn btn-outline-dark">View</button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>

<script>
    import { db } from '../firebaseDb';


    export default {
        components() {
        },
        data() {
            return {
                Users: [],
                activeUser: null,
                Trips: [],
                createdAt: null,
                friends: [],
                friendsTrips: [],
                selectedFriend: null,
                tripTableColumnNames: [
                    {sortId: 1, name: 'Name'},
                    {sortId: 2, name: 'Location'},
                    {sortId: 3, name: 'Start Date'},
                    {sortId: 4, name: 'End Date'}
                ],
                hasTripsToDisplay: false,
                hasFriendsTripsToDisplay: false
            }
        },

        created() {
            db.collection('users').get().then(querySnapshot => {
                querySnapshot.forEach(item => {
                    this.Users.push({
                        key: item.id,
                        firstName: item.data().firstName,
                        lastName: item.data().lastName,
                        email: item.data().email
                    })
                })
            })
        },
        watch: {
            // watch for when the active user is changed
            activeUser() {
                this.queryTrips(this.activeUser)
                this.getFriends(this.activeUser)
                this.friendsTrips = []
                this.hasFriendsTripsToDisplay = false

            }
        },
        methods: {
            selectFriend() {
                this.getFriendsTrips()
            },
            // query trips specific to the active user
            queryTrips(activeUser) {
                this.Trips = [] // reset the trip list to null
                db.collection('trips').where("userId","==", activeUser).get().then(querySnapshot => {
                    querySnapshot.forEach(item => {
                        this.hasTripsToDisplay = true // toggle hasTripsToDisplay to render display
                        this.Trips.push({
                            key: item.id,
                            name: item.data().name,
                            location: item.data().location,
                            startDate: item.data().startDate,
                            endDate: item.data().endDate
                        })
                    })
                })
            },

            getFriends(activeUser) {

                // get user IDs of friends
                this.friends = []
                db.collection('friends').get().then(querySnapshot => {
                    querySnapshot.forEach(item => {
                        if(item.data().userId1 == activeUser || item.data().userId2 == activeUser) {
                            if (item.data().userId1 == activeUser) {

                                db.collection("users").doc(item.data().userId2).get().then(snapshot => {
                                    this.friends.push({
                                        key:snapshot.id,
                                        firstName: snapshot.data().firstName,
                                        lastName: snapshot.data().lastName,
                                        email: snapshot.data().email,
                                    })
                                })


                            } else {
                                db.collection("users").doc(item.data().userId1).get().then(snapshot => {
                                    this.friends.push({
                                        key:snapshot.id,
                                        firstName: snapshot.data().firstName,
                                        lastName: snapshot.data().lastName,
                                        email: snapshot.data().email,
                                    })
                                })
                            }
                        }
                    })
                })
            },
            getFriendsTrips() {
                this.friendsTrips = [] // reset the trip list to null
                db.collection('trips').where("userId","==", this.selectedFriend).get().then(querySnapshot => {
                    querySnapshot.forEach(item => {
                        this.hasFriendsTripsToDisplay = true // set to render component
                        this.friendsTrips.push({
                            key: item.id,
                            name: item.data().name,
                            location: item.data().location,
                            startDate: item.data().startDate,
                            endDate: item.data().endDate
                        })
                    })
                })
            },
            deleteTrip() {
                alert("Doesn't work yet")
            },
            viewTrip() {
                alert("Doesn't work yet")
            }


        }
    }
</script>
<style>
    .btn-primary {
        margin-right: 12px;
    }
</style>
